import { useState, useEffect } from "react";
import Head from "next/head";
import { useRouter } from "next/router";
import axios from "axios";
import SalesForm from "../ui/components/sales/salesFormComponent";
import SalesList from "../ui/components/sales/salesListComponent";
import "../../public/css/general.css";

// ... (mantenha as interfaces ProductVariation e Seller)

export default function SalesPage() {
  // ... (mantenha os estados existentes até loading e error)

  // Estado simplificado para controle de visualização
  const [currentView, setCurrentView] = useState<"form" | "list" | null>(null);

  // ... (mantenha o useEffect, showCustomMessage, handleNewSaleRegistered, pushBackToMenu)

  // Funções mais claras para controle de visualização
  const openForm = () => setCurrentView("form");
  const openList = () => setCurrentView("list");
  const closeView = () => setCurrentView(null);

  // Determina o texto dos botões baseado no estado atual
  const getFormButtonText = () =>
    currentView === "form" ? "Fechar Formulário" : "Adicionar Venda";

  const getListButtonText = () =>
    currentView === "list" ? "Fechar Lista" : "Listar Vendas";

  // Estilo condicional para os botões ativos
  const getButtonClass = (buttonView: "form" | "list") =>
    `btn ${currentView === buttonView ? "primaria" : "secundaria"} px-4 py-2`;

  // ... (mantenha o jwtToken)

  return (
    <div className="d-flex justify-content-between align-items-center flex-column">
      <Head>
        <title>Registro de Vendas - VL Store</title>
      </Head>

      <header className="header-panel position-relative">
        <button
          className="btn primaria position-absolute top-0 end-0 px-3 py-1 shadow"
          onClick={pushBackToMenu}
        >
          Voltar
        </button>
        <img
          className="img logo"
          src="/vl-store-logo-white.svg"
          alt="VL Store Logo"
        />
      </header>

      <div className="container-fluid px-6 pt-5">
        <div className="row mb-4">
          <div className="col d-flex gap-3">
            <button
              className={getButtonClass("form")}
              onClick={() =>
                currentView === "form" ? closeView() : openForm()
              }
            >
              {getFormButtonText()}
            </button>
            <button
              className={getButtonClass("list")}
              onClick={() =>
                currentView === "list" ? closeView() : openList()
              }
            >
              {getListButtonText()}
            </button>
          </div>
        </div>

        {loading && (
          <p className="text-center text-white">Carregando dados...</p>
        )}
        {error && <p className="text-center text-danger">{error}</p>}

        {!loading && !error && (
          <div className="row">
            {currentView === "form" && (
              <div className="col-lg-5 mb-4 mb-lg-0 d-flex flex-column">
                <SalesForm
                  onSaleRegistered={handleNewSaleRegistered}
                  showMessage={showCustomMessage}
                  vendedoresDisponiveis={vendedoresDisponiveis}
                  produtosDisponiveis={produtosDisponiveis}
                  jwtToken={jwtToken || undefined}
                />
              </div>
            )}

            {currentView === "list" && (
              <div className={currentView === "form" ? "col-lg-7" : "col-12"}>
                <SalesList salesData={registeredSales} />
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
